// main page elements
body {
	font-size: 14px;
	// color: $dark
	font-family: 'Gilroy';
	font-weight: 400;
	background: white;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	font-family: 'Gilroy'; }

.container {
	max-width: 1570px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }

.heshteg {
	font-family: Gilroy;
	font-style: normal;
	font-weight: 800;
	font-size: 18px;
	line-height: 130%;
	text-transform: uppercase;
	color: #93AAC0; }
.main_h1 {
	font-family: Gilroy;
	font-style: normal;
	font-weight: bold;
	font-size: 34px;
	line-height: 130%;
	color: #314A60; }
.main_p {
	font-family: Gilroy;
	font-style: normal;
	font-weight: normal;
	font-size: 14px;
	line-height: 150%;
	color: #314A60;
	max-width: 278px; }
.main_button {
	position: relative;
	background: #EA3838;
	border-radius: 5px;
	padding-top: 16px;
	padding-bottom: 16px;
	padding-left: 134px;
	padding-right: 154px;
	color: #fff;
	font-family: Gilroy;
	font-style: normal;
	font-weight: bold;
	font-size: 16px;
	line-height: 150%;
	cursor: pointer;
	margin-top: 35px;
	.img_left {
		position: absolute;
		margin-left: 50px;
		margin-top: -20px; } }
.mg_top {
	margin-top: 84px; }
.button_slider {
	background: #E0EDF5;
	border-radius: 5px;
	padding: 17px;
	&:hover {
		background: #EA3838; } }
//slider
.slick-dots {
	margin-top: 15px;
	list-style: none;
	padding: 0;
	margin: 0;
	display: flex;
	align-items: center;
	justify-content: center; }
.slick-dots {
	li {
		background: #E0EDF5;
		border-radius: 50%;
		width: 10px;
		height: 10px;
		font-size: 5px; } }

.slick-dots {
	button {} }

.slick-dots {
	.slick-active {
		button {
			border-radius: 50%;
			width: 10px;
			height: 10px;
			font-size: 5px;
			background: #44B2FC; } } }
.slick-next {
	right: 10px; }
.slick-prev {
	left: 10px;
	z-index: 99; }
