@mixin flex_column {
    flex-direction: column; }
@include iphone5(portrait) {
    .page-container {
        .container {
            .slider {
                .slick-list {
                    .slick-track {
                        .slick-slide {
                            .block_slider_txt {
                                right: 0px;
                                left: 10px;
                                padding-right: 120px;
                                .slide_title {
                                    font-size: 12px; }
                                p {
                                    font-size: 12px; } } } } } }
            .advantages_left {
                .advantages_blocks {
                    justify-content: center;
                    .advantages_block {
                        text-align: center; } } }
            .advantages_right {
                .main_button {
                    padding-left: 28px;
                    padding-right: 48px; } }
            .about_windows {
                @include flex_column;
                .about_windows_text {
                    padding-left: 10px; }
                .about_windows_media {
                    margin-top: 20px;
                    padding-left: 10px;
                    a {
                        margin-right: 15px; } } }
            .popular_state_main {
                .meta_teg {
                    .a_meta_teg {
                        display: flex;
                        flex-wrap: wrap;
                        justify-content: center; }
                    .main_button {
                        padding-left: 98px;
                        padding-right: 118px; } } }
            .fotter_container {
                .left_fotter {
                    .nav_fotter {
                        .flex {
                            @include flex_column;
                            .nav_fotter_block1, .nav_fotter_block2 {
                                margin-right: 0px; } }
                        p {
                            margin-top: 10px; } }
                    .blog_fotter {
                        margin-right: 0px; } }
                .right_fotter {
                    @include flex_column; } }
            .project {
                .multiple-items {
                    .slick-list {
                        .slick-track {
                            .slick-slide {
                                .block_big {
                                    .block_big_text {
                                        h4 {
                                            text-align: center;
                                            margin-top: 0px;
                                            margin-left: 0px;
                                            font-size: 10px; } } } } } } } }
            .useful {
                .left_block {
                    .main_button {
                        padding-left: 80px;
                        padding-right: 100px; } } }
            .block_state {
                .block_big {
                    max-height: 100%;
                    .img_a {
                        img {
                            max-width: 100%; }
                        a {
                            top: 0; } } }
                .block_two {
                    .sub_block_big {
                        max-height: 100%;
                        .img_a {
                            img {
                                max-width: 100%; }
                            a {
                                top: 0; } } } } } } } }
